.navleiste{

padding: 0px;

text-align: center;
background-color: rgb(79, 139, 216);
}
.navleiste{
  display: flex;
  justify-content: space-between;
  width: 100%;
  position: fixed;
  top:0;
  left:0;
}

.logo img{
height: 80px;
}
.logo{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 80px;
  
}

.logo h1 {
  padding-left: 20px;
  color: white;
 
}

.navleiste ul{
  list-style: none;
  display: flex;
  height:80px;
  justify-content: flex-end;
  align-items: center;
  padding-right: 80px;
  margin:0;
}
.navleiste ul li{
  padding-left: 20px;
  font-size: 2.5rem;
}
.link{
  text-decoration: none;
  color:aliceblue
}
.home{
  text-align: center;
  margin-top: 100px;
  



}

.product{
margin-top: 80px;
  padding-top: 2vh;
  display: flex;
  justify-content: center;
}

.ueberschrift{
  font-size: 3rem;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(33, 77, 160);
  text-shadow: 4px 4px 8px black;
  text-align: center;
}

.prod{
  font-size: 3rem;
  padding: 20px;
}

.zeiten{
  margin-top: 100px;
  margin-bottom: 500px;
  height: calc(100vh-200px)
}

.zeitenWrapper{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
}
.tag{
  display: flex;
  align-items:center ;
  justify-content: center;
  font-size: 2rem;
  margin: 10px;
}
/* .uhrzeit{
  display: flex;
  align-items: center;
} */

.header{
  display: flex;
  flex-direction: column;
  padding-inline: 80px;
  margin-top: 80px;
}
.header h1{
  text-align: center;
  font-size: 6rem;
  margin:10px;
}

.header p{
  font-size: 20px;;
}

.product2{
  position: fixed;
  bottom:0px;
  left:0;
  display: flex;
  justify-content: center;
  background-color: aquamarine;
  width: 100%;
}


.content{
  width: 50%;
  display: flex;
  flex-direction: column;
  }

  .content h2{
    font-weight: 400;
    
  }

  .content img{
   width: 70%;
  }
.home{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.product{
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px
}

.pizza-card{
  width: 20%;
  border: 2px solid green;
  margin-bottom: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.pizzaImage img{
  width: 100%;
}

.pizzaHead{
  display: flex;
  justify-content: center;
}

.pizzaDesc{
  display: flex;
  padding-inline: 30px;
  height:20px
}

.pizzaPrice{
  padding-inline: 30px;
  display: flex;
  justify-content:flex-end;
  
}
.pizzaPrice p{
  font-weight: bold;
  color: red;
  font-size: 2rem;
}